import React, { useState, useEffect } from 'react';
import {  BrowserRouter as Router, Route, Link, Routes  } from "react-router-dom";
import { Button, Tab, Tabs, Unstable_Grid2 as Grid,Typography } from '@mui/material';
import { darken, lighten } from '@mui/system';


import TwitterIcon from '@mui/icons-material/Twitter';
import PolicyIcon from '@mui/icons-material/Policy';


import { CardListTop } from './CardListTop';
import { DeckListTop } from './DeckListTop';
import { HideOnScrollAppBar } from './CustomAppBar';
import { LinkButton } from './CustomButton';


const Card = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(0);
  }, [setTabValue]);
  return <CardListTop />;
};

const Deck = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(1);
  }, [setTabValue]);
  return (
    <Button variant="contained" color="primary" component={Link} to="/cardlist">
  カードリストへ
</Button>

  );
};

const OnePersonTool = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(2);
  }, [setTabValue]);
  return <div>シミュレータ</div>;
};

// ここからフッターコンポーネントを追加します
const Footer = () => {
  return (
    <footer style={{
      padding: '4px 4px',
      borderTop: '1px solid #e7e7e7',
      lineHeight: 1,
      // color: mainColor,
      fontSize: '16px',
    }}>
      <div>画像の著作権は©青山剛昌/小学館 ©TOMYに帰属します。</div>
      <div>
        {/* <LinkButton
          url="https://nohminist.github.io/"
          icon={<HomeIcon />}
        /> */}
        <LinkButton
          url="https://twitter.com/nohminism"
          color={'#1DA1F2'}
          icon={<TwitterIcon />}
        />
        <LinkButton
          text="プライバシーポリシー"
          url="https://nohminist.github.io/privacy-policy/"
          icon={<PolicyIcon />}
        />
      </div>
      <div style={{ margin: '10px' }}>
        <Typography variant="body2" align="center">
          &copy; {new Date().getFullYear()} Nohminism
        </Typography>
      </div>
    </footer>
  );
}


export const Top = () => {
  const [value, setValue] = useState(0)

  return (
    <Router basename="/conan-cardgame"> {/* ベースURLを設定 */}
      <HideOnScrollAppBar>
        <Grid container>
          <Grid>
            <Tabs value={value} onChange={(event, newValue) => setValue(newValue)}>
              <Tab label="カード" component={Link} to="/cardlist" />
              {/* <Tab label="デッキ" component={Link} to="/decklist" />
              <Tab label="シミュレータ" component={Link} to="/simulator" /> */}
            </Tabs>
          </Grid>
        </Grid>
      </HideOnScrollAppBar>
      <Routes>
        <Route path="/" element={<Card setTabValue={setValue} />} />
        <Route path="/cardlist" element={<Card setTabValue={setValue} />} />
        <Route path="/decklist" element={<Deck setTabValue={setValue}/>} />
        <Route path="/simulator" element={<OnePersonTool setTabValue={setValue} />} />
      </Routes>
      <Footer /> 
    </Router>
  );
};
